@import url(https://fonts.googleapis.com/css2?family=Orelega+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navbar-brand{
    font-family: 'Orelega One', cursive;
    font-size: 2rem;
    color: white;
}
#intro-img{
    width: 100%;
}
nav{
    background: rgba(250, 246, 246, 0.8);
    box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.5);
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
}
.intro-div{
    height:100vh;
    background-image: url(https://cdn.pixabay.com/photo/2016/01/19/17/47/hiker-1149898_1280.jpg);
}
.intro-div h1{
    position: absolute;
    top: 50vh;
    right: 16px;
    color: white;
    font-family: Roboto,Helvetica,Arial,sans-serif;
    font-weight:600;
    font-size:54px;
    text-shadow: 1.5px rgba(0,0,0,0.7);
}
.site-footer
{
  background-color:#26272b;
  padding:45px 0 20px;
  font-size:15px;
  line-height:24px;
  color:#737373;
}
.site-footer hr
{
  border-top-color:#bbb;
  opacity:0.5
}
.site-footer hr.small
{
  margin:20px 0
}
.site-footer h6
{
  color:#fff;
  font-size:16px;
  text-transform:uppercase;
  margin-top:5px;
  letter-spacing:2px
}
.site-footer a
{
  color:#737373;
}
.site-footer a:hover
{
  color:#3366cc;
  text-decoration:none;
}
.footer-links
{
  padding-left:0;
  list-style:none
}
.footer-links li
{
  display:block
}
.footer-links a
{
  color:#737373
}
.footer-links a:active,.footer-links a:focus,.footer-links a:hover
{
  color:#3366cc;
  text-decoration:none;
}
.footer-links.inline li
{
  display:inline-block
}
.site-footer .social-icons
{
  text-align:right
}
.site-footer .social-icons a
{
  width:40px;
  height:40px;
  line-height:40px;
  margin-left:6px;
  margin-right:0;
  border-radius:100%;
  background-color:#33353d
}
.copyright-text
{
  margin:0
}
footer{
    padding:20px;
}
@media (max-width:991px)
{
  .site-footer [class^=col-]
  {
    margin-bottom:30px
  }
  
}
@media (max-width:767px)
{
  .site-footer
  {
    padding-bottom:0
  }
  .site-footer .copyright-text,.site-footer .social-icons
  {
    text-align:center
  }
}
.social-icons
{
  padding-left:0;
  margin-bottom:0;
  list-style:none
}
.social-icons li
{
  display:inline-block;
  margin-bottom:4px
}
.social-icons li.title
{
  margin-right:15px;
  text-transform:uppercase;
  color:#96a2b2;
  font-weight:700;
  font-size:13px
}
.social-icons a{
  background-color:#eceeef;
  color:#818a91;
  font-size:16px;
  display:inline-block;
  line-height:44px;
  width:44px;
  height:44px;
  text-align:center;
  margin-right:8px;
  border-radius:100%;
  transition:all .2s linear
}
.social-icons a:active,.social-icons a:focus,.social-icons a:hover
{
  color:#fff;
  background-color:#29aafe
}
.social-icons.size-sm a
{
  line-height:34px;
  height:34px;
  width:34px;
  font-size:14px
}
.social-icons a.facebook:hover
{
  background-color:#3b5998
}
.social-icons a.twitter:hover
{
  background-color:#00aced
}
.social-icons a.linkedin:hover
{
  background-color:#007bb6
}
.social-icons a.dribbble:hover
{
  background-color:#ea4c89
}
@media (max-width:767px)
{
  .social-icons li.title
  {
    display:block;
    margin-right:0;
    font-weight:600
  }
}
.places-grid{
    padding-top: 80px;
    background: -webkit-linear-gradient(135deg,rgba(19, 18, 18, 0.88),rgba(0, 0, 0, 0.45));
}
.places-intro{
    display:grid;
    grid-template-columns: 1fr 1fr;
    color : white;
    -webkit-align-items: center;
            align-items: center;
    margin:10px;
    text-align: left;
    height: 90vh;
    font-family: 'Russo One', sans-serif;
}
.places-intro h1{
    font-size: 3rem;
}
.places-grid li{
    list-style: none;
    padding:10px;
}
a, a:hover, a:focus, a:active {
    text-decoration: none;
    color: inherit;
}
.place-category{
    border-left:6px solid rgb(184, 184, 184);
    font-size:54px;
    color: aliceblue;
    text-align:left;
    padding:60px 10px;
}
.grid-list{
    -webkit-align-items:center;
            align-items:center;
    background:rgba(0,0,0,0.3);
    margin: 10px;
    border-radius: 20px;
}
iframe,.grid-list img{
    width:100%;
    height:100%;
    border: 5px solid black;
    outline: 5px solid black;
    border-radius: 20px;
}
.grid-list img{
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}
.grid-list img:hover { 
    -webkit-transform: scale(1.1); 
    transform: scale(1.1); 
  }
.detail-tile{
    display:grid;
    grid-template-columns: 1fr 1fr;
    -webkit-align-items:center;
            align-items:center;
    padding: 10px;
    grid-gap: 20px;
    gap: 20px;
}
.place_div{
    border-bottom: 10px solid white;
    padding-top: 15px;
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
}
#grid-head{
    color: #24a38e;
    font-size:60px;
    text-shadow:5px 2px black;
}
#grid-head:hover{
    text-shadow:5px 2px black;
    color: white;
}
.place-category:hover{
    text-shadow:5px 2px rgb(31, 114, 103);
}
.place_div:hover{
    text-shadow:2px 2px white;
    cursor:default;
}
@media screen and (max-width:870px){
    .places-intro{
        display:-webkit-flex;
        display:flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content:space-around;
                justify-content:space-around;
        text-align: center;
    }
    .places-intro h1{
        height:150px;
    }
    .places-intro img{
        width: 100%;
    }
    #grid-head{
        padding-bottom:20px;
        border-bottom:2px solid white;
    }
    .detail-tile{
        display:-webkit-flex;
        display:flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        padding:0px;
        margin:2px;
    }
    .detail-tile img,iframe{
        border:none;
        outline:none;
        padding:0px;
        margin:0px;
        width:100%;
    }
    .place-div{
        border-radius: 5px;
    }
}

.about-us{
    margin: 0 10px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    -webkit-align-items: center;
            align-items: center;
}
h6{
    font-size:40px;
}
@media screen and (max-width:870px){
    .about-us{
        display:-webkit-flex;
        display:flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content:space-around;
                justify-content:space-around;
    }
    .about-us img{
        width: 100%;
    }
    #about-head{
        -webkit-order: 2;
                order: 2;
    }
    #about-img{
        -webkit-order:1;
                order:1;
    }
}
