.about-us{
    margin: 0 10px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
}
h6{
    font-size:40px;
}
@media screen and (max-width:870px){
    .about-us{
        display:flex;
        flex-direction: column;
        justify-content:space-around;
    }
    .about-us img{
        width: 100%;
    }
    #about-head{
        order: 2;
    }
    #about-img{
        order:1;
    }
}