@import url('https://fonts.googleapis.com/css2?family=Russo+One&display=swap');
.places-grid{
    padding-top: 80px;
    background: -webkit-linear-gradient(135deg,rgba(19, 18, 18, 0.88),rgba(0, 0, 0, 0.45));
}
.places-intro{
    display:grid;
    grid-template-columns: 1fr 1fr;
    color : white;
    align-items: center;
    margin:10px;
    text-align: left;
    height: 90vh;
    font-family: 'Russo One', sans-serif;
}
.places-intro h1{
    font-size: 3rem;
}
.places-grid li{
    list-style: none;
    padding:10px;
}
a, a:hover, a:focus, a:active {
    text-decoration: none;
    color: inherit;
}
.place-category{
    border-left:6px solid rgb(184, 184, 184);
    font-size:54px;
    color: aliceblue;
    text-align:left;
    padding:60px 10px;
}
.grid-list{
    align-items:center;
    background:rgba(0,0,0,0.3);
    margin: 10px;
    border-radius: 20px;
}
iframe,.grid-list img{
    width:100%;
    height:100%;
    border: 5px solid black;
    outline: 5px solid black;
    border-radius: 20px;
}
.grid-list img{
    transition: transform .2s;
}
.grid-list img:hover {
    -ms-transform: scale(1.1); 
    -webkit-transform: scale(1.1); 
    transform: scale(1.1); 
  }
.detail-tile{
    display:grid;
    grid-template-columns: 1fr 1fr;
    align-items:center;
    padding: 10px;
    gap: 20px;
}
.place_div{
    border-bottom: 10px solid white;
    padding-top: 15px;
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
}
#grid-head{
    color: #24a38e;
    font-size:60px;
    text-shadow:5px 2px black;
}
#grid-head:hover{
    text-shadow:5px 2px black;
    color: white;
}
.place-category:hover{
    text-shadow:5px 2px rgb(31, 114, 103);
}
.place_div:hover{
    text-shadow:2px 2px white;
    cursor:default;
}
@media screen and (max-width:870px){
    .places-intro{
        display:flex;
        flex-direction: column;
        justify-content:space-around;
        text-align: center;
    }
    .places-intro h1{
        height:150px;
    }
    .places-intro img{
        width: 100%;
    }
    #grid-head{
        padding-bottom:20px;
        border-bottom:2px solid white;
    }
    .detail-tile{
        display:flex;
        flex-direction: column;
        padding:0px;
        margin:2px;
    }
    .detail-tile img,iframe{
        border:none;
        outline:none;
        padding:0px;
        margin:0px;
        width:100%;
    }
    .place-div{
        border-radius: 5px;
    }
}
